@font-face {
  font-style: normal;
  font-weight: 300;
  font-family: "Roboto";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Light.woff2") format("woff2"), url("../fonts/RobotoCondensed-Light.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Regular.woff2") format("woff2"), url("../fonts/RobotoCondensed-Regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Bold.woff2") format("woff2"), url("../fonts/RobotoCondensed-Bold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Alumni Sans";
  font-display: swap;
  src: url("../fonts/AlumniSans-SemiBold.woff2") format("woff2"), url("../fonts/AlumniSans-SemiBold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 800;
  font-family: "Alumni Sans";
  font-display: swap;
  src: url("../fonts/AlumniSansExtraBold.woff2") format("woff2"), url("../fonts/AlumniSansExtraBold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Roboto", "Arial", sans-serif;
  color: #102654;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
  scroll-behavior: smooth;
}

body {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #ffffff;
}
body h2 {
  font-size: 54px;
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-weight: 600;
  line-height: normal;
  letter-spacing: 1.62px;
}
@media (max-width: 1199px) {
  body h2 {
    font-size: 42px;
    letter-spacing: 1.26px;
  }
}
@media (max-width: 767px) {
  body h2 {
    font-size: 32px;
    letter-spacing: 0.96px;
  }
}

a {
  color: #102654;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #102654;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

::-webkit-scrollbar {
  width: 0;
}

:root {
  --swiper-navigation-size: 32px;
  --swiper-theme-color: $color-default-sapphire;
}

@media (max-width: 767px) {
  :root {
    --swiper-navigation-size: 19px;
    --swiper-theme-color: $color-default-sapphire;
  }
}
.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Roboto", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.disabled {
  opacity: 0.5;
  pointer-events: none;
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 1160px;
  margin: 0 auto;
  max-width: 1920px;
}
@media (max-width: 1199px) {
  .container {
    width: 100%;
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 4.7%;
  }
}

.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px 20px;
  background-color: transparent;
  color: #102654;
  border: none;
  cursor: pointer;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 12px;
  line-height: 12px;
  font-weight: 700;
  letter-spacing: 0.6px;
  text-transform: uppercase;
  transition: opacity 0.3s ease;
  user-select: none;
}
.btn:focus-visible {
  outline: none;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}

.custom-input {
  position: relative;
  margin-right: 30px;
  display: inline-block;
  width: 190px;
}
@media screen and (max-width: 767px) {
  .custom-input {
    padding: 4px 0 8px;
    width: 100%;
  }
}
.custom-input input {
  flex-grow: 1;
  max-width: 300px;
  min-height: 48px;
  margin-left: auto;
  padding: 10px;
  border: 2px solid rgba(0, 0, 0, 0.2);
  outline: none;
}
@media screen and (max-width: 767px) {
  .custom-input input {
    max-width: unset;
    width: 100%;
  }
}
.custom-input__error {
  position: absolute;
  right: 0;
  bottom: 35px;
  color: #ff1553;
  opacity: 0;
  transition: opacity 0.3s ease;
}
@media screen and (max-width: 767px) {
  .custom-input__error {
    bottom: 20px;
  }
}
.custom-input.is-invalid input {
  border-bottom: 1px solid #ff121f;
}
.custom-input.is-invalid input:focus, .custom-input.is-invalid input:hover {
  border-bottom: 1px solid rgba(255, 18, 31, 0.5);
}
.custom-input.is-valid input {
  border-bottom: 1px solid #22b259;
}
.custom-input.is-valid input:focus, .custom-input.is-valid input:hover {
  border-bottom: 1px solid rgba(34, 178, 89, 0.5);
}
.custom-input .input-message {
  width: 191px;
  font-size: 14px;
  line-height: 16px;
  position: absolute;
  right: 0;
  bottom: -20px;
}
.custom-input .input-message.is-invalid {
  color: #ff121f;
}
.custom-input .input-message.is-valid {
  color: #15ab07;
}
@media (max-width: 767px) {
  .custom-input .input-message {
    bottom: -12px;
  }
}
@media (max-width: 1199px) {
  .custom-input {
    margin-right: 0;
    width: 206px;
  }
}
@media (max-width: 767px) {
  .custom-input {
    padding-bottom: 9px;
    width: 100%;
  }
}

.logo {
  display: flex;
  align-items: center;
  min-height: 44px;
}
.logo:hover, .logo:focus-visible {
  outline: none;
  opacity: 0.5;
}
@media (max-width: 1199px) {
  .logo {
    min-height: 17px;
  }
}

@media (max-width: 1199px) {
  .logo img {
    width: 80px;
    height: 16px;
  }
}
@media (max-width: 767px) {
  .logo img {
    width: 67px;
    height: 24px;
  }
}

.header {
  position: absolute;
  top: 30px;
  left: 0;
  right: 0;
  width: 1160px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
}
@media (max-width: 1199px) {
  .header {
    top: 40px;
    width: 88.28%;
  }
}
@media (max-width: 767px) {
  .header {
    top: 15px;
    width: 90.63%;
    align-items: center;
  }
}

.header__tel {
  font-size: 22px;
  line-height: 32px;
  font-weight: 600;
  color: #ffffff;
}
.header__tel:hover, .header__tel:focus-visible {
  outline: none;
  opacity: 0.5;
}
@media (max-width: 1199px) {
  .header__tel {
    font-size: 20px;
    line-height: 17px;
  }
}
@media (max-width: 767px) {
  .header__tel {
    font-size: 17px;
    line-height: 15px;
    margin-top: 2px;
    letter-spacing: -0.3px;
  }
}

.footer {
  width: 100vw;
  margin-top: auto;
  background-color: #030a1a;
  padding: 41px 0 23px;
}
.footer .container {
  display: flex;
  justify-content: space-between;
}
@media (max-width: 767px) {
  .footer .container {
    display: block;
    column-count: 2;
    column-gap: 32px;
  }
}
.footer img {
  margin-top: -4px;
  width: 88px;
  height: 32px;
}
@media (max-width: 1199px) {
  .footer {
    padding: 44px 0 45px;
  }
  .footer img {
    width: 82px;
    height: 18px;
    margin-top: 0;
  }
}
@media (max-width: 767px) {
  .footer {
    padding: 50px 0 52px;
  }
  .footer img {
    width: 66px;
    height: 15px;
  }
  .footer .logo {
    margin-bottom: 20px;
  }
}

.social {
  margin: 0;
  padding: 0;
  display: flex;
}
@media (max-width: 767px) {
  .social {
    column-span: all;
  }
}

.social__item {
  list-style-type: none;
}
.social__item:not(:last-child) {
  margin-right: 11px;
}

.social__link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 25px;
  height: 25px;
  background-color: #ffffff;
  border-radius: 50px;
}
.social__link svg {
  fill: #030a1a;
}
.social__link:hover, .social__link:focus {
  background-color: #103aae;
  outline: none;
}
.social__link:hover svg, .social__link:focus svg {
  fill: #ffffff;
}
.social__link:active {
  background-color: #102654;
}
.social__link--disabled {
  opacity: 0.3;
}

.source {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  margin-left: 8px;
  padding: 0;
  min-width: 37.16%;
}
@media (max-width: 1199px) {
  .source {
    min-width: 57.67%;
    margin-left: 16px;
  }
}
@media (max-width: 767px) {
  .source {
    margin-left: 0;
    margin-bottom: 45px;
    display: block;
  }
}

.source__item {
  list-style-type: none;
  grid-column: 2/3;
}
.source__item:not(:last-child) {
  margin-right: 20px;
}
@media (max-width: 1199px) {
  .source__item:not(:last-child) {
    margin-right: 10px;
  }
}
@media (max-width: 767px) {
  .source__item {
    margin-bottom: 16px;
    width: 100%;
  }
}

.source__link {
  padding: 5px 10px;
  color: #ffffff;
  font-size: 14px;
}
.source__link:hover {
  border-bottom: 1px solid #ffffff;
}
.source__link:focus-visible {
  outline: none;
  border: 1px solid #ffffff;
  padding-right: 9px;
  padding-left: 9px;
}
.source__link--disabled {
  opacity: 0.4;
}
@media (max-width: 767px) {
  .source__link {
    font-size: 16px;
    padding-left: 0;
  }
  .source__link:focus-visible {
    margin-left: -1px;
    padding-left: 0;
  }
}

.hero {
  display: flex;
  justify-content: space-between;
  padding: 20px 200px 48px 0;
  color: #ffffff;
}
.hero div:first-child {
  margin-top: 122px;
}
@media (max-width: 1199px) {
  .hero div:first-child {
    max-width: 47.79%;
    margin-top: 57px;
  }
}
@media (max-width: 767px) {
  .hero div:first-child {
    margin-top: 0;
    width: 100%;
    max-width: 100%;
  }
}
.hero h2 {
  width: 250px;
  font-size: 92px;
  line-height: 110px;
  font-weight: 800;
  letter-spacing: 4.6px;
  text-transform: uppercase;
  margin: 0;
}
@media (max-width: 1199px) {
  .hero h2 {
    width: 63.8%;
    font-size: 77px;
    line-height: 86px;
    letter-spacing: 0;
    text-transform: initial;
    margin-right: 3px;
  }
}
@media (max-width: 767px) {
  .hero h2 {
    margin-left: -45px;
    width: auto;
  }
}
.hero p {
  font-size: 48px;
  line-height: 37px;
  font-weight: 600;
  letter-spacing: 1.44px;
  margin: 0 51px 25px 0;
}
@media (max-width: 1199px) {
  .hero p {
    font-size: 34px;
    line-height: 25px;
    margin: 0 4px 15px 0;
    letter-spacing: 1.02px;
  }
}
@media (max-width: 767px) {
  .hero p {
    font-size: 28px;
    letter-spacing: 0.84px;
    margin: 0 3px 18px 0;
  }
}
.hero img {
  width: 500px;
  height: 556px;
}
@media (max-width: 1199px) {
  .hero img {
    width: 324px;
    height: 390px;
  }
}
@media (max-width: 767px) {
  .hero img {
    display: none;
  }
}
@media (max-width: 1199px) {
  .hero {
    padding: 42px 0 80px;
  }
}
@media (max-width: 767px) {
  .hero {
    padding-top: 97px;
  }
}

.hero__wrapper {
  display: flex;
  justify-content: space-between;
  margin-bottom: 50px;
}
@media (max-width: 1199px) {
  .hero__wrapper {
    margin-bottom: 38px;
  }
}
@media (max-width: 767px) {
  .hero__wrapper {
    margin-bottom: 23px;
    display: grid;
    grid-template-columns: auto auto;
    min-height: 172px;
  }
}

.hero__list {
  font-family: "Roboto", "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  align-self: flex-end;
  margin: 0 16px 35px 20px;
  padding: 0;
}
@media (max-width: 1199px) {
  .hero__list {
    margin: 0 0 20px 0;
  }
}
@media (max-width: 767px) {
  .hero__list {
    margin: 0 0 31px 0;
  }
}

.hero__item {
  list-style-type: none;
}

.hero__link {
  width: 234px;
  min-height: 61px;
  margin-left: auto;
  border: 4px solid #ffffff;
  color: #ffffff;
}
.hero__link:hover, .hero__link:focus-visible {
  background-color: #ffffff;
  color: #103aae;
}
.hero__link:active {
  color: #102654;
}
@media (max-width: 1199px) {
  .hero__link {
    width: 57.72%;
  }
